import React, { useCallback, useEffect, useRef, useState } from "react";
import cx from "classnames";
import LocationPicker from "components/global-components/Components/LocationPicker";
import { Field, getIn } from "formik";
import { debounce } from "lib/helpers/helpers";
import { add, head, isEmpty } from "lodash";
import isNil from "lodash/isNil";
import replace from "lodash/replace";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";
import styled from "styled-components";

const containerStyle = {
  width: "100%",
  height: "400px",
};

const center = {
  lat: 37.7749, // San Francisco coordinates
  lng: -122.4194,
};

const StyledInput = styled(Input)`
  &::placeholder {
    font-size: 13px; /* You can adjust the font size as needed */
  }
`;

export const GooglePlaceFilter = React.memo(
  ({
    label,
    field,
    form: { touched, errors, setFieldValue },
    type,
    placeholder,
    disabled,
    id,
    onChange,
    childFieldName,
    bsSize = "md",
    hasSearch = false,
    searchType = [],
    m0 = false,
    className,
    required,
    fieldStyle = {},
    isEdit,
    isFilter = false,
    onSelect,
    ...props
  }) => {

    const [partialAddres, setPartialAddress] = useState(field.value);
    const [isInvalidAddress, setIsInvalidAddress] = useState(null);
    const invalid = getIn(touched, field.name) && getIn(errors, field.name);
    const placesAutocompleteRef = useRef();

    useEffect(() => {
      if (partialAddres !== field.value) {
        setPartialAddress(field.value);
      }
    }, [field.value]);

    const handleChange = useCallback((address, latLng) => {
      // if(isEdit) {
      //   setPartialAddress(address);
      // }
      // else{
      //   setFieldValue(field.name,"")
      // }

      setPartialAddress(address);

      // debounce(getGeoCode(address),1000);
      if (isEmpty(address)) {
        setFieldValue(field.name, "");
      }

      !isNil(onChange) && debounce(onChange(address, latLng), 3000);
    });

    const handleSelect = useCallback(async (value) => {
      try {
        const results = await geocodeByAddress(value);
        //const latLng = await getLatLng(results[0]);
        setIsInvalidAddress(null);
        setPartialAddress(value);

        

        // Custom filtering logic to check if the address is complete
        const {address_components: addressComponents, geometry} = results[0];

        const street = addressComponents.find((component) =>
          component.types.includes("route") ? component?.long_name : null
        );
        const city = addressComponents.find((component) =>
          component.types.includes("locality") ? component?.long_name : null
        );
        const state = addressComponents.find((component) =>
          component.types.includes("administrative_area_level_1")
            ? component?.short_name
            : null
        );
        const zipCode = addressComponents.find((component) =>
          component.types.includes("postal_code") ? component?.long_name : null
        );

      
        const geoCode = head(results);
        const bounds = geometry.bounds.toJSON()

        const {
          geometry: { location },
          place_id,
        } = geoCode;
        
        setFieldValue(field.name, geoCode.formatted_address);
        const latLng = {
          lng: location.lng(),
          lat: location.lat(),
        };
        const addressSearch = {
          street: !isNil(street) ? street.long_name : street,
          city: !isNil(city)
            ? isNil(street)
              ? city.long_name
              : undefined
            : city,
          state: !isNil(state) ? isNil(city) ? state.short_name : undefined : state,
          statelong: !isNil(state) ? isNil(city)? state.long_name: undefined : state,
          zipCode: !isNil(zipCode) ? zipCode.long_name : zipCode,
        }
        
        setFieldValue("location", latLng);
        setFieldValue("bounds", bounds);
        setFieldValue("address", addressSearch);

        setFieldValue("placeId", place_id);
        !isNil(onSelect) && onSelect(value, latLng);

        clearSuggestions();
      } catch (error) {
        console.error("Error fetching address details:", error);
      }
    });

    const clearSuggestions = () => {
      // Access clearSuggestions from the PlacesAutocomplete context
      // to manually close the dropdown
      placesAutocompleteRef.current.clearSuggestions();
    };

    // const handleSelect = (address) => {
    //   geocodeByAddress(address)
    //     .then((results) => {
    //       const geoCode = head(results);
    //       const {
    //         geometry: { location },
    //         place_id,
    //       } = geoCode;
    //       setFieldValue(field.name, geoCode.formatted_address);
    //       setFieldValue(
    //         "location",
    //         JSON.stringify({
    //           latitude: location.lat(),
    //           longitude: location.lng(),
    //         })
    //       );

    //       setFieldValue(childFieldName);
    //       setFieldValue("placeId", place_id);

    //       geoCode.geometry.location.lat();
    //
    //       getLatLng(geoCode).then((latLng) => {
    //         setFieldValue(field.name, geoCode.formatted_address);
    //         setFieldValue(childFieldName, latLng);
    //         setFieldValue("placeId", latLng);
    //       });
    //     })
    //     .catch((error) => console.error("Error", error));
    // };

    return (
      <FormGroup className={cx({ "m-md-0": m0 }, className)}>
        {/* <PlacesAutocomplete
        searchOptions={{
          componentRestrictions: { country: ["us"] },
          //types: searchType,
        }}
        value={partialAddres}
        onChange={handleChange}

      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
          <div>
            {label !== undefined && (
              <Label
                className={cx({ requiredField: required })}
                for={props.for || props.label}
              >
                {label}
              </Label>
            )}

            <Input
              bsSize={bsSize}
              {...getInputProps({
                placeholder: "Enter Property Address",
                invalid: invalid,
              })}
              value={isEdit ?partialAddres : field.value}
              {...props}
              className={cx({ "is-invalid": invalid })}
              invalid={invalid}
            />
            <div
              className="autocomplete-dropdown-container "
              style={{ position: "absolute", zIndex: 999 }}
            >
              {loading && <div>Loading...</div>}

              {suggestions.map((suggestion, index) => {
                const { description, active } = suggestion;
                const style = suggestion.active
                  ? { backgroundColor: "#fafafa", cursor: "pointer" }
                  : { backgroundColor: "#ffffff", cursor: "pointer" };
                return (
                  <div
                    key={index}
                    {...getSuggestionItemProps(suggestion, {
                      style,
                    })}
                  >
                    <span>
                      <LocationPicker />
                      {replace(description, ", USA", "")}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </PlacesAutocomplete> */}

        <PlacesAutocomplete
          ref={placesAutocompleteRef}
          searchOptions={{
            componentRestrictions: { country: ["us"] },
          }}
          value={partialAddres}
          onChange={(v) => debounce(handleChange(v), 1000)}
          onSelect={handleSelect}
        >
          {({
            getInputProps,
            suggestions,
            getSuggestionItemProps,
            loading,
          }) => (
            <div style={{ position: "relative", ...fieldStyle }}>
              {/* <input
                {...getInputProps({
                  placeholder: 'Enter a location...',
                  className: 'location-search-input',
                })}
              /> */}
              {label !== undefined && (
                <Label
                  className={cx({ requiredField: required })}
                  for={props.for || props.label}
                >
                  {label}
                </Label>
              )}
              <StyledInput
                bsSize={bsSize}
                {...getInputProps({
                  placeholder: placeholder
                    ? placeholder
                    : "Enter Property Address",
                  invalid: invalid,
                })}
                value={partialAddres}
                {...props}
                className={cx({ "is-invalid": invalid })}
                invalid={invalid}
              />
              {loading ||
                (suggestions.length > 0 && (
                  <div
                    className="autocomplete-dropdown-container"
                    style={{
                      position: "absolute",
                      zIndex: 999,
                      width: "100%",
                      boxShadow: "0px 7px 12px -4px lightgray",
                      padding: "10px",
                      background: "white",
                    }}
                  >
                    {loading && <div>Loading...</div>}
                    {suggestions.map((suggestion) => {
                      const style = suggestion.active
                        ? { backgroundColor: "#fafafa", cursor: "pointer" }
                        : { backgroundColor: "#ffffff", cursor: "pointer" };
                      return (
                        <div
                          {...getSuggestionItemProps(suggestion, { style })}
                          onClick={() => {
                            handleSelect(suggestion.description);
                          }}
                        >
                          {suggestion.description}
                        </div>
                      );
                    })}
                  </div>
                ))}
            </div>
          )}
        </PlacesAutocomplete>
        {invalid && (
          <FormFeedback className="mt-0 mb-1 d-block">
            {getIn(errors, field.name)}
          </FormFeedback>
        )}

        {isInvalidAddress && (
          <FormFeedback className="mt-0 mb-1 d-block">
            {isInvalidAddress}
          </FormFeedback>
        )}
      </FormGroup>
    );
  }
);

