import API, { makeQueryString } from "./BaseAPI";
import {
  Get,
  GetSecured,
  PUTSecuredFormData,
  DeleteSecured,
  PutSecured,
} from "../client/REST";
import { uri as image_uri, uri_image_gallery } from "./images";
const uri = "listings";
const delete_uri = "delete_listing";
const search_uri = "search";
const multiple_ids = "listing_ids";
const object_key = "listing";
const my = "my";
const favourite = "favourite";

const BaseAPI = new API({
  uri,
  delete_uri,
  search_uri,
  multiple_ids,
  object_key,
});

export default {
  ...BaseAPI,
  get: ({ id }) => Get(`/${uri}/id/${id}`),
  getPublishedList: ({ params }) =>
    Get(
      `/${uri}?${makeQueryString({
        ...params,
      })}`
    ),
  getMyListing: ({ params }) => GetSecured(`/${uri}/${my}`, params),
  getAdminApproveListing: ({ params }) => GetSecured(`/${uri}/approve-listings`, params),
  getMyFavouriteList: ({ params }) => GetSecured(`/${uri}/${favourite}`, params),
  updateApproveListing: ({ id, params, }) => {
    return PutSecured(`/${uri}/approve-listings/${id}`, {
      "listing" : params,
    });
  },
  updateWithFormData: ({ params }) => {
    return PUTSecuredFormData(`/${image_uri}/${uri_image_gallery}`, {
      ...params,
    });
  },
  deleteListing: ({ id }) => DeleteSecured(`/${uri}/id/${id}`),
};
