export const getIdMode = ({pathname}) => {
  const id = pathname.split("/")[2];

  return typeof id === "string" && id === "create" ? null : id;
};

export const getMode = (props) => {
  const idAsMode = getIdMode({...props.location});
  if (idAsMode) {
    return {
      mode: "edit",
    };
  } else {
    return {
      mode: "create",
    };
  }
};
